import React, { Component } from 'react';
import { Link } from './Link';
import { resolve } from 'path';

export class Header extends Component {
    state = {
        openNav: false
    };

    handleOpenNav = () => {
        this.setState({ openNav: !this.state.openNav });
    };

    render() {
        return (
            <header className="header sticky-top">
                <nav className="navbar navbar-expand-lg navbar-light bg-light py-0 shadow-sm">
                    <div className="container">
                        <Link className="navbar-brand" to={this.props.path}>
                            <strong>{this.props.logo}</strong>
                        </Link>
                        <button className="navbar-toggler border-0" onClick={this.handleOpenNav}>
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className={`collapse navbar-collapse${this.state.openNav ? ' show' : ''}`}>
                            <div className="navbar-nav nav-tabs ml-auto">
                                <Link exact className="nav-item nav-link" to={this.props.path}>Home</Link>
                                <Link className="nav-item nav-link" to={resolve(this.props.path, 'books')}>Books</Link>
                                <Link className="nav-item nav-link" to={resolve(this.props.path, 'about')}>About</Link>
                                <Link className="nav-item nav-link" to={resolve(this.props.path, 'login')}>Login</Link>
                            </div>
                        </div>
                    </div>
                </nav>
            </header>
        );
    }
} 